import React from "react";
import { Box, Container, Grid, Link, Typography, useMediaQuery, Theme } from "@mui/material";
import { Link as RouterLink } from 'react-router-dom';

const links = [
  {
    label: "Home",
    href: "/"
  },
  {
    label: "Account",
    href: "/account"
  },
  {
    label: "Cpu4sale",
    href: "/cpusale"
  },
  {
    label: "LimitlessWAX",
    href: "/limitlesswax"
  },
  {
    label: "GitHub",
    href: "https://github.com/"
  },
  {
    label: "Discord",
    href: "https://discord.com/"
  },
  {
    label: "Privacy",
    href: "https://www.privacy.com"
  },
  {
    label: "Terms",
    href: "https://www.term.com/"
  },
];
const Footer: React.FC = () => {
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));

  return (
    <Box
      component="footer"
      sx={{
        bgcolor: "#331E22",// Dark brown background
        color: "#E9E9E9",
        py: 4,
        // Hide on mobile
        display: { xs: 'none', sm: 'block' },
        // Or with conditional visibility
        visibility: isMobile ? 'hidden' : 'visible',
        // Responsive opacity
        opacity: { xs: 0, sm: 1 },
        // Smooth transition
        transition: 'opacity 0.3s ease-in-out'
      }}
    >
      <Container maxWidth="lg">
        <Box display="flex" justifyContent="space-between" gap={2}>
          {/* Logo and Help Section */}
          <Box sx={{ width: 200, display: 'flex', flexDirection: 'column', gap: 2 }}>
            <img src="images/Limitless1.png" alt="logo" width={50} />
            <Typography variant="body2" sx={{ fontWeight: 400, fontSize: { xs: '8px', md: '14px' }, lineHeight: '100%', letterSpacing: '0%' }}>
              If you need help or have any questions, please go through our{" "}
              <Link href="#" color="inherit" underline="hover">
                Discord
              </Link>.
            </Typography>
            {/* Copyright Section */}
            <Box >
              <Typography sx={{ fontWeight: 400, fontSize: { xs: '8px', md: '14px' }, lineHeight: '100%', letterSpacing: '0%' }}>
                LimitlessWAX Copyright  {new Date().getFullYear()}
              </Typography>
            </Box>
          </Box>
          {/* Navigation Links */}
          <Box sx={{ flex: 1, p: 2 }}>
            <Box sx={{ display: 'flex', flexWrap: 'wrap' }}>
              {links.map((link) => (
                <Box key={link.label} width={"25%"} py={2} textAlign={"center"}>
                  <Link href={link.href} color="inherit" underline="hover">
                    {link.label}
                  </Link>
                </Box>
              ))}
            </Box>
          </Box>


        </Box>
      </Container >
    </Box >
  );
};

export default Footer;